import { useState } from 'react';
import { Bars3Icon, XMarkIcon } from '@heroicons/react/24/solid';

import { SelectedPage } from '../../shared/types';

import useMediaQuery from '../../hooks/useMediaQuery';

import ActionButton from '../../shared/ActionButton';

import Logo from '../../assets/Logo.png';

import Link from './Link';

type Props = {
  isTopOfPage: boolean;
  selectedPage: SelectedPage;
  setSelectedPage: (value: SelectedPage) => void;
};

const Navbar = ({ isTopOfPage, selectedPage, setSelectedPage }: Props) => {
  const flexBetween = 'flex items-center justify-between';
  const [isMenuToggled, setIsMenuToggled] = useState<boolean>(false);
  const isAboveMediumScreens = useMediaQuery('(min-width: 1060px)');
  const navbarBackground = isTopOfPage ? '' : 'bg-red-100 drop-shadow';

  const closeMenuToggled = (): void => {
    setIsMenuToggled(!isMenuToggled);
  };

  return (
    <nav>
      <div
        className={`${navbarBackground} ${flexBetween} fixed top-0 z-30 w-full py-6`}
      >
        <div className={`${flexBetween} mx-auto w-5/6`}>
          <div className={`${flexBetween} w-full gap-16`}>
            {/* LEFT SIDE */}
            <img alt='logo' src={Logo} className={'w-24'} />

            {/* RIGHT SIDE */}
            {isAboveMediumScreens ? (
              <div className={`${flexBetween} w-full`}>
                <div className={`${flexBetween} gap-8 text-sm`}>
                  <Link
                    page=''
                    selectedPage={selectedPage}
                    setSelectedPage={setSelectedPage}
                  />
                  <Link
                    page=''
                    selectedPage={selectedPage}
                    setSelectedPage={setSelectedPage}
                  />
                  <Link
                    page=' '
                    selectedPage={selectedPage}
                    setSelectedPage={setSelectedPage}
                  />
                  <Link
                    page=''
                    selectedPage={selectedPage}
                    setSelectedPage={setSelectedPage}
                  />
                </div>
                <div className={`${flexBetween} gap-8`}>
                  <p></p>
                  <ActionButton setSelectedPage={setSelectedPage}>
                    
                  </ActionButton>
                </div>
              </div>
            ) : (
              <button
                className='rounded-full bg-rose-900 p-2'
                onClick={() => setIsMenuToggled(!isMenuToggled)}
              >
                <Bars3Icon className='h-6 w-6 text-white' />
              </button>
            )}
          </div>
        </div>
      </div>

      {/* MOBILE MENU MODAL */}
      {!isAboveMediumScreens && isMenuToggled && (
        <div className='fixed right-0 bottom-0 z-40 h-full w-[300px] bg-red-100 drop-shadow-xl'>
          {/* CLOSE ICON */}
          <div className='flex justify-end p-12'>
            <button onClick={() => setIsMenuToggled(!isMenuToggled)}>
              <XMarkIcon className='h-6 w-6 text-neutral-900' />
            </button>
          </div>

          {/* MENU ITEMS */}
          <div className='ml-[13%] flex flex-col gap-10 text-2xl'>
            <Link
              closeMenuToggled={closeMenuToggled}
              page=''
              selectedPage={selectedPage}
              setSelectedPage={setSelectedPage}
            />
            <Link
              page=''
              selectedPage={selectedPage}
              setSelectedPage={setSelectedPage}
            />
            <Link
              page=' '
              selectedPage={selectedPage}
              setSelectedPage={setSelectedPage}
            />
            <Link
              page=''
              selectedPage={selectedPage}
              setSelectedPage={setSelectedPage}
            />
          </div>
        </div>
      )}
    </nav>
  );
};

export default Navbar;
